.sideview-functions {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 13px;
  height: 18px;
  .item {
    height: 18px;
    font-size: 16px;
    margin-left: 10px;
    color: $text-gray;
    &.edit {
      cursor: pointer;
      :hover {
        color: $text;
      }
    }
    &.active {
      color: $text;
    }
    &.delete {
      cursor: pointer;
      :hover {
        color: $warning;
      }
    }
    svg {
      height: 18px;
    }
  }
}
.sideview-item {
  // max-width: 650px;
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .sideview-item-menus {
    width: 100%;
    max-width: 650px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    height: 40px;
    .tabs {
      display: flex;
      .tab {
        margin-right: 20px;
        a {
          cursor: pointer;
          text-transform: uppercase;
          font-family: Calibri-bold;
          font-size: 21px;
          color: $text-gray;

          &:hover {
            color: $main;
          }
        }
        &.selected {
          a {
            color: $main;
            padding-bottom: 6px;
            position: relative;
            &:after {
              content: "";
              position: absolute;
              bottom: 0;
              left: 0;
              right: 0;
              background: $main;
              height: 4px;
              border-radius: 10px;
            }
          }
        }
      }
    }
    .action {
      input {
        cursor: pointer;
        text-transform: uppercase;
        border: 2px solid $main;
        background-color: transparent;
        padding: 8px 14px 8px 14px;
        font-size: 14px;
        font-weight: bold;
        border-radius: 10px;
        color: $main;

        &:hover {
          background-color: $main;
          color: white;
        }
      }
    }
  }
  .sideview-content {
    margin-top: 74px;
    max-width: 650px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    .sideview-content-title {
      font-size: 41px;
      display: flex;

      .avatar {
        margin-right: 20px;
        a {
          font-size: 45px;
          font-family: Calibri-bold;
          color: white;
          width: 100px;
          height: 100px;
          border-radius: 20px;
          background-color: #f1935c;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      input {
        border: none;
        background: transparent;
        color: $text;
        width: 100%;

        &::placeholder {
          color: $text-gray;
          opacity: 50;
        }
      }
    }
    .sideview-content-item {
      font-size: 21px;
      padding-top: 20px;
      line-height: 32px;
      flex-direction: column;
      display: flex;

      label {
        text-transform: uppercase;
        font-size: 16px;
        color: $text-gray;
        flex-grow: 0.3;
      }

      #coached-by {
        display: flex;
        .tag {
          font-size: 16px;
          border-radius: 10px;
          border: 2px solid $main;
          padding: 0px 10px 0px 10px;
          margin-right: 10px;
        }
      }

      input {
        border: none;
        background: transparent;
        flex-grow: 1;
        // width: 100%;
        color: $text;

        &::placeholder {
          color: $text-gray;
          opacity: 50;
        }
      }

      textarea {
        // width: 100%;
        border: none;
        background: transparent;
        outline: none;
        overflow: auto;
      }

      select {
        border: none;
        background-color: transparent;
        width: fit-content;

        &.hide-icon {
          -webkit-appearance: none;
          -moz-appearance: none;
          text-indent: 1px;
          text-overflow: "";
        }
      }
      #hide-icon {
        -webkit-appearance: none;
        -moz-appearance: none;
        text-indent: 1px;
        text-overflow: "";
      }
    }
  }
}

@media (max-width: 980px) {
  .sideview-item-menus {
    // flex-direction: column;

    .action {
      // margin-top: 20px;
    }
  }
}
