.header {
  // height: 120px;
  // padding: 30px;

  // display: flex;
  // flex-direction: column;
  // justify-content: space-between;
  box-shadow: 0px -1px 10px #70707038;
  .header-state {
    display: flex;
    align-items: center;
    padding: 30px 30px 10px 30px;
    .header-state-title {
      font-family: "Calibri-bold";
      font-size: 26px;
      flex-grow: 1;
    }
    .header-state-count {
      flex-grow: 10;
      i {
        color: $text-gray;
        font-family: "Calibri-bold";
        font-style: normal;
        font-size: 18px;
        background-color: $secondary;
        padding: 0px 15px 0px 15px;
        border-radius: 10px;

        // padding: 10px;
      }
    }
    .header-state-new {
      cursor: pointer;
      width: 30px;
      height: 30px;
      border-radius: 10px;
      background-color: $main;

      display: flex;
      justify-content: center;
      align-items: center;

      a {
        font-size: 28px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  .header-search {
    display: flex;
    padding: 10px 30px 10px 30px;

    .header-search-search {
      padding-left: 5px;
      padding-right: 5px;
      color: $text-gray;
      background-color: $secondary;
      border-bottom-left-radius: 5px;
      border-top-left-radius: 5px;

      display: flex;
      align-items: center;

      svg {
        height: 23px;
      }
    }

    input {
      background: $secondary;
      border: none;
      // border-radius: 5px;
      padding-top: 7px;
      padding-bottom: 7px;
      width: 100%;
      font-size: 17px;

      &::placeholder {
        color: $text-gray;
        opacity: 50;
      }
    }
    .header-search-filter {
      padding-left: 5px;
      padding-right: 5px;
      color: $text-gray;
      background-color: $secondary;
      border-bottom-right-radius: 5px;
      border-top-right-radius: 5px;

      display: flex;
      align-items: center;
      cursor: pointer;

      &:hover {
        color: $text;
      }
      &.active {
        color: $text;
      }
    }
  }
  .header-filter {
    display: flex;
    padding: 10px 0px 20px 30px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .item {
      color: $text-gray;
      font-family: Calibri-bold;
      cursor: pointer;
      font-size: 18px;
      background-color: $secondary;
      padding: 4px 14px 4px 14px;
      border-radius: 10px;
      margin-right: 15px;
      text-transform: uppercase;
      white-space: nowrap;
      &:hover {
        background-color: $main;
        color: white;
      }
      &.selected {
        background-color: $main;
        color: white;
      }
    }
  }
}
