.list {
  height: calc(100vh - 181px);
  overflow-y: auto;

  &::-webkit-scrollbar {
    display: none;
  }
  .list-item {
    padding: 15px 30px 15px 30px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    // margin-bottom: 30px;

    &:hover {
      background-color: #edf7ff;
    }
    &.selected {
      background-color: #edf7ff;
    }
    .list-item-icon {
      border-radius: 20px;
    }
    .list-item-data {
      margin-left: 10px;
      .list-item-title {
        color: $text;
        font-size: 21px;
        display: inline-block;
        width: 230px;
        white-space: nowrap;
        overflow: hidden !important;
        text-overflow: ellipsis;
      }
      .list-item-read-time {
        color: $text-gray;
        font-size: 18px;
      }
    }
  }
}
