.container {
  display: grid;
  // background-color: #70707038;
  // grid-gap: 1px;
  grid-template-columns: 374px 10px auto;
  // grid-template-rows: auto auto;
  grid-template-areas: "list divider side";

  &.collapsed {
    grid-template-columns: 20px 10px auto;

    .content-list {
      display: none;
    }
    // .content-right {
    //   .sideview-item {
    //     .sideview-item-menus {
    //       max-width: 700px;
    //     }
    //     .sideview-content {
    //       max-width: 700px;
    //     }
    //   }
    // }
  }

  .content-list {
    grid-area: list;
  }
  .content-divider {
    grid-area: divider;
  }
  .content-right {
    grid-area: side;
  }

  .content-list {
    background-color: white;

    &.hidden {
      display: none;
    }
  }

  .content-divider {
    background-color: #fcfdff;
    border-left: 1px solid $lines;
    cursor: w-resize;
  }

  .content-right {
    background-color: #fcfdff;
    padding: 30px;
    overflow-y: auto;
  }
}

@media (max-width: 980px) {
  .container {
    .content-right {
      padding: 15px !important;
    }
  }
}
