.navbar {
  background-color: $main;
  color: $white;
  width: 90px;
  z-index: 900;

  display: flex;
  flex-flow: column;
  flex: 0 0 auto;
  // flex-grow: 1;

  .logo {
    font-size: 13pt;
    color: #ffffff;
    flex-grow: 1;
    text-decoration: none;

    display: flex;
    align-items: flex-end;
    justify-content: center;
  }

  .main {
    display: flex;
    flex-flow: column;
    flex-grow: 20;
    justify-content: center;
    .route {
      display: flex;
      flex-flow: row;
      align-items: center;
      justify-content: center;
      flex-grow: 0.05;
      .icon {
        display: flex;
        padding: 17px;
        text-decoration: none;
        font-size: 14pt;
        font-style: normal;
        border-radius: 20px;
        color: $white;

        svg {
          height: 24px;
          width: 24px;
        }
      }

      &.active {
        .icon {
          background-color: $active !important;
        }
      }
    }
  }
  .profile {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-grow: 1;

    img {
      border-style: none;
      height: 60px;
      width: 60px;
      border-radius: 20px;
    }
  }
}

@media (max-width: 940px) {
  .navbar {
    position: fixed;
    margin: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    height: 70px;
    .logo {
      display: none;
      flex-grow: 0;
    }
    .settings {
      display: none;
    }
    .profile {
      display: none;
    }

    .main {
      flex-flow: row;
      justify-content: space-evenly;
      .route {
        flex-flow: column;
      }
    }
  }
}
