$montserrat-font-dir: "../fonts/montserrat/";
$calibri-font-dir: "../fonts/calibri/";
$icon-dir: "../fonts/icons/";

/*===============================================================================================*/
/*		                                  FONT - CALIBRI REGULAR							         		        */
/*===============================================================================================*/
@font-face {
  font-family: "Calibri";
  src: url($calibri-font-dir+"Calibri Regular.ttf")
    format("truetype");
}

@font-face {
  font-family: "Calibri-bold";
  src: url($calibri-font-dir+"Calibri Bold.ttf")
    format("truetype");
}

@font-face {
  font-family: "Calibri-light";
  src: url($calibri-font-dir+"Calibri Light.ttf")
    format("truetype");
}

@font-face {
  font-family: "Calibri-italic";
  src: url($calibri-font-dir+"Calibri Italic.ttf")
    format("truetype");
}
