.divider {
  // position: absolute;
  // top: 0;
  // right: 0;
  border-radius: 20px;
  border: 2px solid #63bb67;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  margin-top: 300px;
  margin-left: -13px;
  background-color: white;
  cursor: pointer;

  &:hover {
    background-color: $main;
    svg {
      color: white;
    }
  }
  svg {
    color: $main;
  }
}
